import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useParams
} from "react-router-dom";
import Jumbotron from 'react-bootstrap/Jumbotron';
import Table from 'react-bootstrap/Table';

const CLUES = [
  "Sarah hates me because she ain't me. I am big and baggy and never off your shoulders",
  'Painted me black for the craic, but now I look wile tacky',
  'At 27 years old Conor still has no idea how to use me.',
  'You would be literally lost without me and I would be lost without Jim',
  'Fra Hates cannot stand me!',
  'Always running but have no legs ',
  'You hear me beeping all the time but my main purpose is for heating',
  'Me and Elton John are best mates so we are',
  'At 27 years old Claire still can’t make a decent cup of me',
  'As soon as everyone heard you bought me people started thinking you were preggers'
];

function Example() {
  return (
  <div>
    <Router>
      <Switch>
        <Route path="/:id" >
          <App />
        </Route>
      </Switch>
    </Router>
  </div>
  );
}

function App() {
  let { id } = useParams();
  // using local storage to persist through each URL click - probably can be done better 
  return (
    <div className="App">
      <ClueInformation id={id} />
      <CompletedClues />
    </div>
  );
}

function CompletedClues() {
  const listItems = CLUES.map((clue, index) =>
    <tr>
      <td>{localStorage.getItem(index) ? clue : null}</td>
      <td>{localStorage.getItem(index) ? `✅` : null}</td>
    </tr>
  );
  // localStorage.clear();

  return (
    <Table striped bordered hover>
      <thead>
      <tr>
        <th>Completed Clues</th>
      </tr>
      </thead>
      <tbody>
      {listItems}
      </tbody>
    </Table>
  )
}

function ClueInformation(props) {
  const isFirstClue = (props.id == 0)
  return (
    <Jumbotron>
      {isFirstClue && <h3> Welcome to your eletronic easter egg hunt! To start on your journey round the house
        </h3>}
      <h1>{CLUES[props.id]}</h1>
    </Jumbotron>
  )
}

export default Example;